import React from "react";
import Paginator from "../common/Paginator/Paginator";
import User from "./User";


let Users = ({currentPage, totalUsersCount, pageSize, onPageChanget, users, ...props}) => {

    return <div>
        <Paginator currentPage={currentPage} totalItemsCount={totalUsersCount} pageSize={pageSize}
                   onPageChanget={onPageChanget} portionSize={10}/>
        <div>
            {
                users.map(u => <User user={u} follow={props.follow}
                                     followingInProgress={props.followingInProgress}
                                     unfollow={props.unfollow}/>)
            }
        </div>
    </div>

}
export default Users